
.main-carousel {
  position: absolute;
  left: 0;
  top: 0;
  width: 1226px;
  background: #fff;
  height: 460px;
  z-index: 1;
}
.main-carousel .container {
    position: relative;
    height: 460px;
}
.main-carousel .container .fade-item {
      position: absolute;
      left: 0;
      top: 0;
      width: 1226px;
      z-index: 1;
}
.main-carousel .container .play-pre {
      position: absolute;
      left: 234px;
      top: 50%;
      margin-top: -35px;
      z-index: 2;
}
.main-carousel .container .play-pre .icon {
        width: 41px;
        height: 69px;
        background: url(../assets/icon-slides.png) no-repeat;
        background-position: -83px 50%;
        display: inline-block;
        cursor: pointer;
}
.main-carousel .container .play-pre .icon:hover {
          background: url(../assets/icon-slides.png) no-repeat;
          background-position: 1px 50%;
}
.main-carousel .container .play-next {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -35px;
      z-index: 2;
}
.main-carousel .container .play-next .icon {
        width: 41px;
        height: 69px;
        background: url(../assets/icon-slides.png) no-repeat;
        background-position: -124px 50%;
        display: inline-block;
        cursor: pointer;
}
.main-carousel .container .play-next .icon:hover {
          background: url(../assets/icon-slides.png) no-repeat;
          background-position: -42px 50%;
}
.main-carousel .container .play-dot {
      position: absolute;
      z-index: 1;
      width: 100px;
      right: 20px;
      bottom: 20px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.main-carousel .container .play-dot .dot {
        width: 6px;
        height: 6px;
        border: 2px solid rgba(255, 255, 255, 0.5);
        border-radius: 50%;
        background: rgba(0, 0, 0, 0.5);
}
.main-carousel .container .play-dot .active {
        border-color: rgba(0, 0, 0, 0.5);
        background: rgba(255, 255, 255, 0.5);
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



