
.star-goods {
  width: 1226px;
  margin: 0 auto;
  margin-top: 26px;
}
.star-goods h2 {
    font-size: 22px;
    font-weight: 200;
    line-height: 58px;
    color: #333;
}
.star-goods .goods-container {
    height: 340px;
    width: 1226px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.star-goods .goods-container .goods-item {
      width: 234px;
      height: 300px;
      background: #fafafa;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
